import { Component, Input, OnDestroy } from '@angular/core';
import { NbThemeService, NbColorHelper } from '@nebular/theme';
import { StatisticDataService } from "../../../@core/data-services/statistic.service"
import { timer } from 'rxjs';

import * as moment from 'moment';


@Component({
  selector: 'ngx-chartjs-line',
  template: `
    <chart type="line" [data]="data" [options]="options"></chart>
  `,
})
export class ChartjsLineComponent implements OnDestroy {
  data: any;
  options: any;
  colors: any;
  themeSubscription: any;
  timerSubscription: any;
  chartjs: any;
  dataService: StatisticDataService;

  @Input() dataSource: string;

  constructor(private theme: NbThemeService,dataService: StatisticDataService) {
    this.themeSubscription = this.theme.getJsTheme();
    this.dataService = dataService;
    this.themeSubscription = this.theme.getJsTheme().subscribe(config => {
      this.colors = config.variables;
      this.chartjs = config.variables.chartjs;
      this.setOptions(1000);
      this.updateData();
    });
  }

  ngOnInit() {
    console.log("ngOnInit: 7 day review");
    // getting first data  
    this.updateData();

    // adding refresh tasks
    const refresh = timer(5000, 60000);
    this.timerSubscription = refresh.subscribe(val => {
      console.log("Refreshing data: 7 day review");
      this.setOptions(0);
      this.updateData();
    });
  }

  private setOptions(animationDuration:number) {
    var colors = require('nice-color-palettes');
    this.options = {
      animation: { duration: animationDuration },
      responsive: true,
      maintainAspectRatio: false,
      scales: {
        xAxes: [
          {
            gridLines: {
              display: true,
              color: this.chartjs.axisLineColor,
            },
            ticks: {
              fontColor: this.chartjs.textColor,
            },
          },
        ],
        yAxes: [
          {
            gridLines: {
              display: true,
              color: this.chartjs.axisLineColor,
            },
            ticks: {
              fontColor: this.chartjs.textColor,
            },
          },
        ],
      },
      legend: {
        labels: {
          fontColor: this.chartjs.textColor,
        },
      },
    };
  }

  private updateData() {
    //var colors = require('nice-color-palettes');
    this.dataService.getTenantNumbers(this.dataSource).subscribe(res => {
      // hint: https://github.com/designcourse/angular-chartjs/blob/master/src/app/app.component.ts
      let labels = [];
      let data1 = [];
      let data2 = [];
      let data3 = [];
      let data4 = [];
      res.forEach((x) => {
        labels.push(x.TimeUtc);
        data1.push(x.UsersToCheck);
        data2.push(x.UsersInvited);
        data3.push(x.UsersChanged);
        //data1.push(x.AllUsers);
      });
      this.data = {
        labels: labels,
        datasets: [{
          data: data1,
          label: "Checked users",
          backgroundColor: NbColorHelper.hexToRgbA(this.colors.primary, 0.3),
          borderColor: this.colors.primary,   
        },
        {
          data: data2,
          label: "Invited users",
          backgroundColor: NbColorHelper.hexToRgbA(this.colors.danger, 0.3),
          borderColor: this.colors.danger,
        },
        {
          data: data3,
          label: "Changed users",
          backgroundColor: NbColorHelper.hexToRgbA(this.colors.info, 0.3),
          borderColor: this.colors.info,
        }
      ],
      };
    });
  }

  ngOnDestroy(): void {
    this.themeSubscription.unsubscribe();
    this.timerSubscription.unsubscribe();
  }
}
