import { Component, Input, OnDestroy } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import { StatisticDataService } from "../../../@core/data-services/statistic.service"
import { observable, timer } from 'rxjs';
import { DataSource } from 'ng2-smart-table/lib/data-source/data-source';
import * as moment from 'moment';



@Component({
  selector: 'ngx-chartjs-pie',
  template: `<chart type="pie" [data]="data" [options]="options"></chart>
  `
})
export class ChartjsPieComponent implements OnDestroy {
  data: any;
  options: any;
  themeSubscription: any;
  timerSubscription: any;
  chartjs: any;
  dataService: StatisticDataService;
  
  @Input() dataSource: string;
  
  constructor(private theme: NbThemeService, dataService: StatisticDataService) {
    this.themeSubscription = this.theme.getJsTheme();
    this.dataService = dataService;
    this.themeSubscription = this.themeSubscription.subscribe(config => {
      this.chartjs = config.variables.chartjs;
      this.setOptions(1000);
    });
  }
  
  ngOnInit() {
    console.log("ngOnInit", this.dataSource);
    // getting first data  
    this.updateData();

    // adding refresh tasks
    const refresh = timer(5000, 60000);
    this.timerSubscription = refresh.subscribe(val => {
      console.log("Refreshing data:", this.dataSource);
      this.setOptions(0);
      this.updateData();
    });
  }

  private setOptions(animationDuration:number) {
    var colors = require('nice-color-palettes');
    this.options = {
      animation: { duration: animationDuration },
      maintainAspectRatio: false,
      responsive: true,
      scales: {
        xAxes: [
          {
            display: false,
          },
        ],
        yAxes: [
          {
            display: false,
          },
        ],
      },
      legend: {
        labels: {
          fontColor: this.chartjs.textColor,
        },
      },
    };
  }

  private updateData() {
    let sourceRow="";
    switch (this.dataSource) {
      case "AllUsers":
        sourceRow="AllUsers";
        break;
      case "UsersToCheck":
        sourceRow="UsersToCheck";
        break;
      default:
        sourceRow="AllUsers"
        break;
    }
    var colors = require('nice-color-palettes');
    this.dataService.getTenantAllUsers(this.dataSource).subscribe(res => {
      // hint: https://github.com/designcourse/angular-chartjs/blob/master/src/app/app.component.ts
      
      let labels = [];
      let data = [];
      res.forEach((x) => {
        labels.push(x.TenantName);
        data.push(x[sourceRow]);
      });
      this.data = {
        labels: labels,
        datasets: [{
          data: data,
          backgroundColor: colors[0].concat(colors[1]),
        }],
      };
    });
  }

  ngOnDestroy(): void {
    this.themeSubscription.unsubscribe();
    this.timerSubscription.unsubscribe();
  }
}
