import { Component, OnDestroy } from '@angular/core';
import { NbThemeService, NbColorHelper } from '@nebular/theme';
import { StatisticDataService } from "../../../@core/data-services/statistic.service"
import { observable, timer } from 'rxjs';


@Component({
  selector: 'ngx-chartjs-bar',
  template: `
    <chart type="bar" [data]="data" [options]="options"></chart>
  `,
})
export class ChartjsBarComponent implements OnDestroy {
  data: any;
  options: any;
  themeSubscription: any;
  timerSubscription: any;
  chartjs: any;
  colors: any;
  dataService: StatisticDataService;

  constructor(private theme: NbThemeService,dataService: StatisticDataService) {
    this.themeSubscription = this.theme.getJsTheme();
    this.dataService = dataService;
    this.themeSubscription = this.themeSubscription.subscribe(config => {
      this.colors = config.variables;
      this.chartjs = config.variables.chartjs;
      this.setOptions(1000);
    });
   }

  ngOnInit() {
    console.log("ngOnInit");
    // getting first data  
    this.updateData();

    // adding refresh tasks
    const refresh = timer(5000, 60000);
    this.timerSubscription = refresh.subscribe(val => {
      console.log("Refreshing data: Distinct actions");
      this.setOptions(0);
      this.updateData();
    });
  }

  private updateData() {
    var colors = require('nice-color-palettes');
    this.dataService.getDistinctOpertations().subscribe(res => {
      // hint: https://github.com/designcourse/angular-chartjs/blob/master/src/app/app.component.ts
      let labels = [];
      let data1 = [];
      let data2 = [];
      let data3 = [];
      let data4 = [];
      res.forEach((x) => {
        labels.push(x.TenantName);
        data1.push(x.INVITE);
        data2.push(x.CHANGE);
        data3.push(x.DELETE);
        //data1.push(x.AllUsers);
      });
      this.data = {
        labels: labels,
        datasets: [{
          data: data1,
          label: 'Invited',
          backgroundColor: NbColorHelper.hexToRgbA(this.colors.primaryLight, 0.8),
        }, {
          data: data2,
          label: 'Changed',
          backgroundColor: NbColorHelper.hexToRgbA(this.colors.danger, 0.8),
        }, {
          data: data3,
          label: 'Deleted',
          backgroundColor: NbColorHelper.hexToRgbA(this.colors.infoLight, 0.8),
        }],
      };

    });
  }
  private setOptions(animationDuration:number) {
    var colors = require('nice-color-palettes');
      this.options = {
        animation: { duration: animationDuration },
        maintainAspectRatio: false,
        responsive: true,
        legend: {
          labels: {
            fontColor: this.chartjs.textColor,
          },
        },
        scales: {
          xAxes: [
            {
              gridLines: {
                display: false,
                color: this.chartjs.axisLineColor,
              },
              ticks: {
                fontColor: this.chartjs.textColor,
              },
            },
          ],
          yAxes: [
            {
              gridLines: {
                display: true,
                color: this.chartjs.axisLineColor,
              },
              ticks: {
                fontColor: this.chartjs.textColor,
              },
            },
          ],
        },
      };
  }

  ngOnDestroy(): void {
    this.themeSubscription.unsubscribe();
    this.timerSubscription.unsubscribe();
  }
}
